body{
    
    display: flex;
    justify-content: center;
}
.container
{
    width:800px;
    border:1px solid black;
    padding:10px;
    height: 500%;
    background-color: black;
    
}
@media screen  and (max-width:600px) {
    .container
    {
        width:350px;
        border:1px solid black;
        padding:10px;
        height: 500%;
        background-color: black;
    
    }
    body
    {
      display: flex;
      justify-content: center;
    }
}
.list-group-item
{
display:flex;
justify-content: space-between;
}
button:disabled
{
    opacity: 0;
}
#task
{
    border-top-left-radius:30px ;
    border-bottom-left-radius: 30px;
}